.card {
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px 20px;
  height: 200px;
  color: white;
  background: center;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: 0.5s;
  cursor: pointer;
}
.card:hover {
  margin-top: -10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  border: 1px solid rgb(0, 225, 255);
}

.card h1 {
  position: absolute;
  bottom: 10px;
}

